import React from "react";
import { Modal, Icon, Button, Checkbox } from "semantic-ui-react";

export default class WelcomeModal extends React.Component {
  state = {
    open: localStorage.getItem("dontShowAgain")
  };

  closeModal() {
    this.setState({ open: true });
  }

  setDontShowAgain() {
    localStorage.setItem("dontShowAgain", true);
  }

  render() {
    return (
      <Modal open={!this.state.open}>
        <Modal.Header>Hello Recruiter!</Modal.Header>
        <Modal.Content>
          <p>Hello Recruiter how are you?</p>
          <p>
            My name is Rodrigo Santos da Silva and I am a software developer.
            This site is my online resume, where I got all relevant info about
            me such as my skills, education, projects and blog posts.
          </p>
          <p>
            If you think that my profile match what you need please contact me.
            You can send me a e-mail through the contact section or you can also
            see my contacts on the top of the site.
          </p>
          <p>Thank you for looking my resume and Have a nice day!</p>
        </Modal.Content>
        <Modal.Actions>
          <Checkbox
            className="left-align"
            label="Don't show me again"
            onChange={() => this.setDontShowAgain()}
          />
          <Button onClick={() => this.closeModal()} color="green">
            <Icon name="thumbs up" /> Take me to your awesome site!
          </Button>
        </Modal.Actions>
      </Modal>
    );
  }
}



