import React from "react";
import { Form, Icon, Modal, Button } from "semantic-ui-react";
import axios from "axios";

export default class Contact extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      email: "",
      subject: "",
      body: "",
      modalOpen: false,
      sentResult: ""
    };
    this.change = this.change.bind(this);
    this.send = this.send.bind(this);
    this.closeModal = this.closeModal.bind(this);
  }

  change(event) {
    this.setState({ [event.target.name]: event.target.value });
  }

  closeModal() {
    this.setState({ modalOpen: false });
  }

  send() {
    var emailContent = {
      email: this.state.email,
      subject: this.state.subject,
      html: `${this.state.body} email: ${this.state.email}`
    };
    axios
      .post("https://kaizen-email.azurewebsites.net/", emailContent)
      .then(() =>
        this.setState({ modalOpen: true, sentResult: "Your e-mail was sent!" })
      )
      .catch(() =>
        this.setState({
          modalOpen: true,
          sentResult:
            "Ops... We had a problem to send the e-mail. Could you try again in a few minutes?"
        })
      );
  }

  render() {
    return (
      <Form>
        <Modal open={this.state.modalOpen} size="small" closeIcon>
          <Modal.Header>E-mail</Modal.Header>
          <Modal.Content>{this.state.sentResult}</Modal.Content>
          <Modal.Actions>
            <Button onClick={this.closeModal} primary>
              OK
            </Button>
          </Modal.Actions>
        </Modal>
        <Form.Field>
          <label>E-mail</label>
          <input
            name="email"
            value={this.state.email}
            onChange={this.change}
            placeholder="Ex: rh@agoodcompany.com"
          />
        </Form.Field>
        <Form.Field>
          <label>Subject</label>
          <input
            name="subject"
            value={this.state.subject}
            onChange={this.change}
            placeholder="Ex: Job offer"
          />
        </Form.Field>
        <Form.TextArea
          name="body"
          value={this.state.body}
          onChange={this.change}
          label="Body"
          placeholder="Ex: I want to hire you now!"
        />
        <Form.Button onClick={this.send} positive>
          <Icon name="send" />
          Send
        </Form.Button>
      </Form>
    );
  }
}



